.row {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.flex {
  display: flex;
  flex-direction: column;
}

.partner-description {
  text-align: justify;
  margin: 1em 0;
}

.partner-logo {
  justify-self: center;
  align-self: center;
  margin-left: 2em;
}

.align-center a {
  text-align: center;
}

.partners-logo {
  width: 150px;
  margin: 0.5em;
  cursor: pointer;
  text-align: center;
}

@media (max-width: 1080px) {
  .video {
    width: 470px;
    height: 315px;
  }
}

@media (max-width: 768px) {
  .video {
    width: 313px;
    height: 210px;
  }
}
