.intro {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  background-attachment: fixed;
  height: calc(50vh - 100px);
  min-height: 300px;
  text-align: center;
  color: white;
  font-weight: 600;
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 100%;

  &.intro-high {
    height: calc(80vh - 100px);
  }

  &-title {
    font-size: 4rem;
  }
  &-subtitle {
    font-size: 2rem;
  }
}
