.faq-element {
  margin: 2em 0;
}

.faq-element-hr {
  border-top: 2px solid #ffcd33;
  margin: 0.5em 0;
  width: 100%;
}

.faq-element-question {
  font-weight: 300;

  &:hover {
    cursor: pointer;
  }

  &::after {
    content: "+";
    float: right;
    padding-right: 0.5em;
  }

  &.active {
    &::after {
      content: "-";
    }
  }
}

.faq-element-hide {
  display: none;
}

.faq-element-hide.active {
  display: block;
  animation: sweep 0.5s ease-in-out;
}

@keyframes sweep {
  0% {
    opacity: 0;
    margin-top: -10px;
  }
  100% {
    opacity: 1;
    margin-top: 0px;
  }
}
