@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
*{box-sizing:border-box;margin:0;padding:0;border:0;outline:0}body{font-family:"Lato", sans-serif;line-height:1.5}#root{display:flex;flex-direction:column;min-height:100vh}main{flex:1 1}p{font-family:"Open Sans", sans-serif}a,button{text-decoration:none;color:inherit;cursor:pointer;font-style:italic}ul{list-style:none}img{max-width:100%;height:auto}.container{max-width:1158px;margin:auto}section{padding:3em 1.5em;width:100%;height:100%}.section-title{color:#ffcd33;font-weight:600;font-size:2em;margin-bottom:1.5em;text-transform:uppercase}.center{text-align:center}@media (max-width: 768px){.hide-mobile{display:none !important}}@media (min-width: 768px){.hide-desktop{display:none !important}}

header{position:-webkit-sticky;position:sticky;top:0;left:0;width:100%;background-color:white;box-shadow:5px 2px 30px grey}header nav{height:100px;position:relative;z-index:0;display:flex;justify-content:space-between;align-items:center}header nav .nav-logo{margin-left:1.5em}header nav .nav-toggler{margin-right:1.5rem;width:25px;height:25px;display:none;cursor:pointer}header nav .nav-list{display:flex;padding:1.5em}header nav .nav-list .nav-item{padding:1em}header nav .nav-list .nav-item a{position:relative}header nav .nav-list .nav-item a::after{border-radius:1em;border-top:0.2em solid white;content:"";position:absolute;right:100%;bottom:-0.5em;left:0;transition:right 0.4s cubic-bezier(0, 0.5, 0, 1),border-color 0.4s ease-out}header nav .nav-list .nav-item a:hover::after{border-color:#ffcd33;right:0}header nav .nav-list .nav-submenu{display:none;position:absolute;top:100px;background:white;padding:0.5em;width:100px}header nav .nav-list .nav-submenu.active{display:block}header nav .nav-list .nav-submenu-item{padding:1em 0}header nav .nav-icon{width:25px;height:25px}@media (max-width: 768px){.nav-toggler{display:block !important}ul{flex-direction:column;width:100%}.nav-collapse{text-align:center;width:100%;display:none;position:absolute;top:100px;background:#fff1c7;z-index:99}.nav-collapse.active{display:flex}}

.intro{background-repeat:no-repeat;background-size:cover;background-position:center center;background-attachment:fixed;height:calc(50vh - 100px);min-height:300px;text-align:center;color:white;font-weight:600;display:flex;justify-content:center;align-items:center;max-width:100%}.intro.intro-high{height:calc(80vh - 100px)}.intro-title{font-size:4rem}.intro-subtitle{font-size:2rem}

.iconfield{display:flex;flex-direction:column;justify-content:space-between}.iconfield-wrap{display:flex;flex-direction:column}.iconfield-row{display:flex;margin-top:1em;margin-bottom:1em}.iconfield-icon{width:1.5em;height:1.5em;margin-right:1em}.iconfield-title{text-transform:uppercase;color:#ffcd33}.iconfield-content{text-align:justify;margin:1em 0}.iconfield-link{justify-self:stretch;color:#ffcd33}.iconfield-link::after{border-radius:1em;border-top:0.1em solid white;content:"";position:absolute;right:100%;bottom:-0.2em;left:0;transition:right 0.4s cubic-bezier(0, 0.5, 0, 1),border-color 0.4s ease-out}.iconfield-link:hover::after{border-color:#ffcd33;right:0}@media (max-width: 768px){.iconfields{display:flex !important;flex-direction:column}}

.section-divider{border:none;border-top:dotted 5px;border-color:#646464;margin:0 1em}.partners-logo{width:150px;margin:0.5em;cursor:pointer;justify-self:center}.align-center a{text-align:center}

.btn{display:inline-block;padding:0.75em 1.5em;margin-top:1em;border:0.2em solid;text-decoration:none;font-weight:300;text-align:center;transition:all 0.25s;font-style:normal;border-radius:10px}.btn-primary{background-color:#ffcd33;border-color:white;color:white}.btn-primary:hover{background-color:white;border-color:#ffcd33;color:black}.btn-secondary{background-color:#ffcd33;border-color:white;color:white}.btn-secondary:hover{background-color:transparent;border-color:#ffcd33;color:white}

.slider{position:relative;z-index:99;min-height:6em}.slider .slide-author{font-style:italic;float:right;margin:0.5em 0}.wrap{max-width:calc(100% - 2 * 50px);margin:auto;text-align:justify}@media (min-width: 768px){.wrap{max-width:512px}}.prev,.next{cursor:pointer;position:absolute;top:50%;width:auto;margin-top:-22px;padding:16px;font-weight:bold;font-size:18px;transition:0.25s ease;border-radius:0 3px 3px 0;-webkit-user-select:none;-ms-user-select:none;user-select:none}.next{right:0;border-radius:3px 0 0 3px}.prev:hover,.next:hover{background-color:#ffcc33;color:white}

.grid{display:grid;grid-template-columns:repeat(auto-fill, minmax(250px, 1fr));margin:3em 0;grid-gap:0px}

.row{display:grid;grid-template-columns:repeat(2, 1fr)}.flex{display:flex;flex-direction:column}.partner-description{text-align:justify;margin:1em 0}.partner-logo{justify-self:center;align-self:center;margin-left:2em}.align-center a{text-align:center}.partners-logo{width:150px;margin:0.5em;cursor:pointer;text-align:center}@media (max-width: 1080px){.video{width:470px;height:315px}}@media (max-width: 768px){.video{width:313px;height:210px}}

.intro{background-image:url(/static/media/event.465bd543.jpg)}.description-block{margin:3em 0}.description-block .description-title{color:#ffcc33;margin-bottom:1em}

.faq-element{margin:2em 0}.faq-element-hr{border-top:2px solid #ffcd33;margin:0.5em 0;width:100%}.faq-element-question{font-weight:300}.faq-element-question:hover{cursor:pointer}.faq-element-question::after{content:"+";float:right;padding-right:0.5em}.faq-element-question.active::after{content:"-"}.faq-element-hide{display:none}.faq-element-hide.active{display:block;-webkit-animation:sweep 0.5s ease-in-out;animation:sweep 0.5s ease-in-out}@-webkit-keyframes sweep{0%{opacity:0;margin-top:-10px}100%{opacity:1;margin-top:0px}}@keyframes sweep{0%{opacity:0;margin-top:-10px}100%{opacity:1;margin-top:0px}}


.team{display:grid;grid-template-columns:repeat(auto-fill, minmax(200px, 1fr));grid-gap:2em;text-align:center}.team-member{display:flex;flex-direction:column}.team-member-title{margin-top:1em}.team-member-subtitle{margin-top:0.5em;margin-bottom:2em;font-size:0.75rem}.team-member-hr{width:30%;position:relative;left:35%;border-top:3px solid #ffcd33;margin-top:1em}.team-member-icon{width:2em}

footer{padding:3em 1.5em;background-color:#ffcd33;color:white;width:100%;min-height:100px;overflow:hidden}footer .grid{display:grid;grid-template-columns:2fr 2fr;grid-gap:3em}footer .grid .column{flex:0 0 auto}footer .grid .column .footer-title{margin-bottom:1em}footer .grid .column .row{display:flex}footer .grid .column .row .footer-icon{width:2em;margin-right:1em;margin-top:1em;margin-bottom:1em}footer .grid .footer-menu{display:flex;flex-direction:column}@media (max-width: 768px){.grid{display:flex !important;flex-direction:column}}

