@import "../config";

.team {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  grid-gap: 2em;
  text-align: center;

  &-member {
    display: flex;
    flex-direction: column;

    &-title {
      margin-top: 1em;
    }
    &-subtitle {
      margin-top: 0.5em;
      margin-bottom: 2em;
      font-size: 0.75rem;
    }
    &-hr {
      width: 30%;
      position: relative;
      left: 35%;
      border-top: 3px solid $primary;
      margin-top: 1em;
    }
    &-icon {
      width: 2em;
    }
  }
}
