@import "../config";

.iconfield {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  &-wrap {
    display: flex;
    flex-direction: column;
  }

  &-row {
    display: flex;
    margin-top: 1em;
    margin-bottom: 1em;
  }

  &-icon {
    width: 1.5em;
    height: 1.5em;
    margin-right: 1em;
  }

  &-title {
    text-transform: uppercase;
    color: $primary;
  }

  &-content {
    text-align: justify;
    margin: 1em 0;
  }

  &-link {
    justify-self: stretch;
    color: $primary;
    &::after {
      border-radius: 1em;
      border-top: 0.1em solid white;
      content: "";

      position: absolute;
      right: 100%;
      bottom: -0.2em;
      left: 0;
      transition: right 0.4s cubic-bezier(0, 0.5, 0, 1),
        border-color 0.4s ease-out;
    }

    &:hover::after {
      border-color: #ffcd33;
      right: 0;
    }
  }
}

@media (max-width: 768px) {
  .iconfields {
    display: flex !important;
    flex-direction: column;
  }
}
