header {
  position: sticky;
  top: 0;
  left: 0;
  width: 100%;
  background-color: white;
  box-shadow: 5px 2px 30px grey;

  nav {
    height: 100px;
    position: relative;
    z-index: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .nav-logo {
      margin-left: 1.5em;
    }

    .nav-toggler {
      margin-right: 1.5rem;
      width: 25px;
      height: 25px;
      display: none;
      cursor: pointer;
    }

    .nav-list {
      display: flex;
      padding: 1.5em;
      .nav-item {
        padding: 1em;

        a {
          position: relative;

          &::after {
            border-radius: 1em;
            border-top: 0.2em solid white;
            content: "";

            position: absolute;
            right: 100%;
            bottom: -0.5em;
            left: 0;
            transition: right 0.4s cubic-bezier(0, 0.5, 0, 1),
              border-color 0.4s ease-out;
          }

          &:hover::after {
            border-color: #ffcd33;
            right: 0;
          }
        }
      }

      .nav-submenu {
        display: none;
        position: absolute;
        top: 100px;
        background: white;
        padding: 0.5em;
        width: 100px;

        &.active {
          display: block;
        }

        &-item {
          padding: 1em 0;
        }
      }
    }
    .nav-icon {
      width: 25px;
      height: 25px;
    }
  }
}

@media (max-width: 768px) {
  .nav-toggler {
    display: block !important;
  }
  ul {
    flex-direction: column;
    width: 100%;
  }

  .nav-collapse {
    text-align: center;
    width: 100%;
    display: none;
    position: absolute;
    top: 100px;
    background: #fff1c7;
    z-index: 99;

    &.active {
      display: flex;
    }
  }
}
