@import url("https://fonts.googleapis.com/css2?family=Open+Sans&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");

@import "config";

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
}

body {
  font-family: "Lato", sans-serif;
  line-height: 1.5;
}
#root {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
main {
  flex: 1;
}

p {
  font-family: "Open Sans", sans-serif;
}

a,
button {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
  font-style: italic;
}

ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}

.container {
  max-width: 1158px;
  margin: auto;
}

section {
  padding: 3em 1.5em;
  width: 100%;
  height: 100%;
}

.section-title {
  color: $primary;
  font-weight: 600;
  font-size: 2em;
  margin-bottom: 1.5em;
  text-transform: uppercase;
}

.center {
  text-align: center;
}

@media (max-width: 768px) {
  .hide-mobile {
    display: none !important;
  }
}
@media (min-width: 768px) {
  .hide-desktop {
    display: none !important;
  }
}
