@import "../../config";
footer {
  padding: 3em 1.5em;
  background-color: $primary;
  color: white;
  width: 100%;
  min-height: 100px;
  overflow: hidden;
  .grid {
    display: grid;
    grid-template-columns: 2fr 2fr;
    grid-gap: 3em;
    .column {
      flex: 0 0 auto;

      .footer-title {
        margin-bottom: 1em;
      }

      .row {
        display: flex;

        .footer-icon {
          width: 2em;
          margin-right: 1em;
          margin-top: 1em;
          margin-bottom: 1em;
        }
      }
    }

    .footer-menu {
      display: flex;
      flex-direction: column;
    }
  }
}

@media (max-width: 768px) {
  .grid {
    display: flex !important;
    flex-direction: column;
  }
}
