.slider {
  position: relative;
  z-index: 99;
  min-height: 6em;

  .slide {
    &-author {
      font-style: italic;
      float: right;
      margin: 0.5em 0;
    }
  }
}

.wrap {
  max-width: calc(100% - 2 * 50px);
  margin: auto;
  text-align: justify;
}

@media (min-width: 768px) {
  .wrap {
    max-width: 256px * 2;
  }
}

.prev,
.next {
  cursor: pointer;
  position: absolute;
  top: 50%;
  width: auto;
  margin-top: -22px;
  padding: 16px;
  font-weight: bold;
  font-size: 18px;
  transition: 0.25s ease;
  border-radius: 0 3px 3px 0;
  user-select: none;
}

.next {
  right: 0;
  border-radius: 3px 0 0 3px;
}

.prev:hover,
.next:hover {
  background-color: #ffcc33;
  color: white;
}
