@import "../config";

.btn {
  display: inline-block;
  padding: 0.75em 1.5em;
  margin-top: 1em;
  border: 0.2em solid;
  text-decoration: none;
  font-weight: 300;
  text-align: center;
  transition: all 0.25s;
  font-style: normal;
  border-radius: 10px;

  &-primary {
    background-color: $primary;
    border-color: white;
    color: white;

    &:hover {
      background-color: white;
      border-color: $primary;
      color: black;
    }
  }

  &-secondary {
    background-color: $primary;
    border-color: white;
    color: white;

    &:hover {
      background-color: transparent;
      border-color: $primary;
      color: white;
    }
  }
}
